nav {
  background-color: black;
  padding: 10px;
}
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-around;
}
li {
  display: inline;
  margin-right: 10px;
}
a {
  text-decoration: none;
  color: white;
}
a:hover {
  border-bottom: 2px solid white;
}
